<template>
  <div>
		<div>
			<ul style="text-align:left">
				<li>과제분류</li>
				<li><v-btn>낱말연습</v-btn></li>
				<li><v-btn @click="get_dictation">받아쓰기</v-btn></li>
				<li><v-btn>기타</v-btn></li>
			</ul>
		</div>
		<div>
			<ul style="text-align:left">
				<li>과제명</li>
				<li><input type="text" placeholder="제목을 입력해주세요" style="border"></li>
			</ul>
		</div>
		<div>
			<ul>
				<li>과제시작날짜/</li>
				<li>과제마감날짜/</li>
				<li><a href="">문제불러오기</a></li>
				<li>문제셀렉트박스</li>
			</ul>
		</div>
		
		<div class='problem' v-for="(problem,i) of problems" :key="i"> 
			<div class='idx'>{{i + 1}}번</div>
			<div class='problem-text' @keyup.enter="problemAdd"><input v-model="problem.dictationSentence" type="text" name="test_text" placeholder="문제를 입력해주세요" maxlength="20"></div>
			<div class='problem-del' @click="problemDel(i)"><a><i class="far fa-minus-square fa-lg"></i></a></div>
			<voice-recorder :idx='i' @audio-change="audioChange" class="recorder"></voice-recorder>
		</div>
		
		<div class='problem-add' @click="problemAdd"><li><a><i class="far fa-plus-square fa-lg"></i></a></li></div>
		<v-btn class='problems-create' @click="createProblems">과제생성</v-btn>
  </div>
</template>

<script>
import VoiceRecorder from '@/components/VoiceRecord.vue'
import Axios from 'axios'
export default {
	name: 'classhwcreate',
	components:{VoiceRecorder: VoiceRecorder},
	data(){
		return {
			problems:[{dictationSentence:'', audioString: null, homeworkNo:4}]
		}
	},
	methods:{
		problemAdd(){
			this.problems.push({dictationSentence:'', audioString: null, homeworkNo:4})
		},
		problemDel(idx){
			this.problems.splice(idx,1)
		},
		audioChange(i,base64Data){
			this.problems[i].audioString = base64Data
		},
		createProblems(){
			Axios.post(`/dictation/dictation`,this.problems)
			.then(() => {
				console.dir('생성완료')
			})
			.catch(()=>{
				alert('error')
			})
		}
	}
}
</script>

<style scoped>
ul {
	list-style: none;
}
li {
	display: inline;
	text-align: -webkit-match-parent;
}
.problem{
	display: flex;
	justify-content: center;
	align-items: center;
}
.recorder{
	width:100px;
	height: 50px;
}
</style>
