<template>
  <v-app id="app" style="background-color:#FFF7AF">
    <!-- <router-link to="/">Home</router-link> |
    <router-link to="/studentmain">StudentMain</router-link> |
    <router-link to="/teachermain">TeacherMain</router-link>  -->
    <router-view/>
  </v-app>
</template>

<script>
import axios from 'axios'

export default {
  created(){
    if(this.$session.get('jwt')!=null){
          const jwt = this.$session.get('jwt');
          console.dir(axios.defaults)
          axios.defaults.headers.common['Authorization'] = jwt;
          // console.dir(axios.defaults.headers)
          // jwt값을 디코딩 후 userId, no, type을 vuex에 다시 저장 
          axios.get('/user/reget').then((Response)=>{
            this.$store.dispatch('LOGIN',Response.data); 
          })
      // axios.defaults.withCredentials = true;//CORS 요청시에도 클라이언트의 쿠키값 전달
    }
  },
  computed : {
    userInfo() {
      return this.$store.getters.userInfo
    }
  }
}
</script>

