<template>
  <div>
    <template>
    <v-row justify="center">
        <v-dialog
        v-model="dialog"
        persistent
        max-width="600px"
        >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
            >
            받아쓰기생성
            </v-btn>
        </template>
        <v-card>
            <v-card-title>
            <span class="headline">받아쓰기</span>
            </v-card-title>
            <v-card-text>
            <v-container>
                <v-row>
                <v-textarea
                        background-color="light-blue"
                        color="black"
                        label="문제 이름"
                        v-model="name"
                ></v-textarea>
                <v-col cols="12" v-for="i in 10" :key="i">
                     <v-textarea
                        background-color="light-blue"
                        color="black"
                        :label="'문제'+i+'번'"
                        v-model="select[i]"
                    ></v-textarea>
                </v-col>
                </v-row>
            </v-container>
            </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="blue darken-1"
                text
                @click="dialog = false"
            >
                닫기
            </v-btn>
            <v-btn
                color="blue darken-1"
                text
                @click="addDictionSet"
            >
                제출
            </v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>
    </v-row>
    </template>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            dialog : false,
            loading: false,
            search: null,
            select: [],
            name: "",
            dictations : ['Oregon',
          'Palau',
          'Pennsylvania',
          'Puerto Rico',
          '']
        }
    },
    watch: {
      search (val) {
        val && val !== this.select && this.querySelections(val)
      },
    },
    methods: {
      querySelections (v) {
        this.loading = true
        // Simulated ajax query
        setTimeout(() => {
          this.items = this.states.filter(e => {
            return (e || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
          })
          this.loading = false
        }, 500)
      },
      addDictionSet(){
        if(this.select.length!=11){
            alert('모두 입력해주세요1')
            return;
        }
        for(let i=1;i<11;i++){
            if(this.select[i]==null){
                 alert('모두 입력해주세요2')
                 return;
            }
        }
        this.dialog = false
        let data = {
          dictationsetName : this.name,
          dictationSetContentList : this.select
        }
        
        axios.post('dictation/createproblem',data)
        .then((Response)=>{
            Response
            alert('문제 완성')
            this.$router.go();
        })
      },
    },
}
</script>

