<template>
	<v-app>
		<nav-bar></nav-bar>
		<v-row>
			<div class="card green" v-for="child in childrenInfo" :key="child.userNo">
				<div class="additional">
					<div class="user-card">
						<br>
						<v-avatar size="60%">
							<v-img src="../assets/images/boy.png">
							</v-img>
						</v-avatar><br>
						<v-btn @click="delparent(child.userNo)">삭제</v-btn>
						<a href="/childrendetail">자세히보기</a>
					</div>
					<div class="more-info">
						<h1>{{child.userName}}</h1>
						<div class="coords">
							<span>무언가</span>
							<span>쓰고싶다</span>
						</div>
						<div class="coords">
							<span>무언가를</span>
							<span>써야햇</span>
						</div>
						<div class="stats">
							<div>
								<div class="title">글자공부</div>
								<i class="fa fa-trophy"></i>
								<div class="value">100%</div>
							</div>
							<div>
								<div class="title">낱말연습</div>
								<i class="fa fa-gamepad"></i>
								<div class="value">50%</div>
							</div>
							<div>
								<div class="title">받아쓰기</div>
								<i class="fa fa-group"></i>
								<div class="value">0%</div>
							</div>
						</div>
					</div>
				</div>
				<div class="general">
					<h1>{{child.userName}}</h1>
					<p>{{child.birthDate}}</p>
					<p>{{child.nickName}}</p>
				</div>
			</div>
		</v-row>

		<foot-bar></foot-bar>
	</v-app>
</template>

<script>
import axios from 'axios'
import NavBar from '@/components/NavBar.vue'
import FootBar from '@/components/FootBar.vue'

export default {
	name: 'mychildren',
	components: {
		NavBar,
		FootBar,
	},
	data(){
		return {
			childrenInfo :[]
		}
	},
	created(){
		this.get_childrenInfo()
	},
	methods : {
		get_childrenInfo(){
			axios.get('user/childreninfo')
			.then((Response)=>{
				this.childrenInfo = Response.data
			})
			.catch((error)=>{
				error
			})
		},
		delparent(childno){
			axios.delete('user/child/'+childno)
			.then((response)=>{
				response
				alert('삭제되었습니다.')
				this.$router.go();
			})
		}
	}
}
</script>

<style scoped>
.card {
  width: 30%;
  height: 40%;
  background-color: #fff;
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0,0,0,0.4);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  margin: 1.5rem;
}

.card h1 {
  text-align: center;
}

.card .additional {
  position: absolute;
  width: 150px;
  height: 100%;
  background: linear-gradient(#dE685E, #EE786E);
  transition: width 0.4s;
  overflow: hidden;
  z-index: 2;
}

.card.green .additional {
  background: linear-gradient(#92bCa6, #A2CCB6);
}


.card:hover .additional {
  width: 100%;
  border-radius: 0 5px 5px 0;
}

.card .additional .user-card {
  width: 150px;
  height: 100%;
  position: relative;
  float: left;
}

.card .additional .user-card::after {
  content: "";
  display: block;
  position: absolute;
  top: 10%;
  right: -2px;
  height: 80%;
  border-left: 2px solid rgba(0,0,0,0.025);
}

.card .additional .user-card .level,
.card .additional .user-card .points {
  top: 15%;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: bold;
  background: rgba(0,0,0,0.15);
  padding: 0.125rem 0.75rem;
  border-radius: 100px;
  white-space: nowrap;
}

.card .additional .user-card .points {
  top: 85%;
}

.card .additional .user-card svg {
  top: 50%;
}

.card .additional .more-info {
  width: 300px;
  float: left;
  position: absolute;
  left: 150px;
  height: 100%;
}

.card .additional .more-info h1 {
  color: #fff;
  margin-bottom: 0;
}

.card.green .additional .more-info h1 {
  color: #224C36;
}

.card .additional .coords {
  margin: 0 1rem;
  color: #fff;
  font-size: 1rem;
}

.card.green .additional .coords {
  color: #325C46;
}

.card .additional .coords span + span {
  float: right;
}

.card .additional .stats {
  font-size: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  top: auto;
  color: #fff;
}

.card.green .additional .stats {
  color: #325C46;
}

.card .additional .stats > div {
  flex: 1;
  text-align: center;
}

.card .additional .stats i {
  display: block;
}

.card .additional .stats div.title {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

.card .additional .stats div.value {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.card .additional .stats div.value.infinity {
  font-size: 2.5rem;
}

.card .general {
  width: 300px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  padding: 1rem;
  padding-top: 0;
}

.card .general .more {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 0.9em;
}



</style>