import Vue from 'vue'
import VueRouter from 'vue-router'

import Main from '../views/Main.vue'
import ContentsMain from '../views/ContentsMain.vue'
import Draw from '../components/Draw.vue'
import AlphabetWriting from '../components/AlphabetWriting.vue'
import ClassSearch from '../views/ClassSearch.vue'

import Logo from '../components/Logo.vue'
// 글자공부
import Alphabet from '../components/Alphabet/Alphabet.vue'
import KoreanDetail from '../components/Alphabet/KoreanDetail.vue'
import KoreanConsonant from '../components/Alphabet/KoreanConsonant.vue'
import KoreanDoubleConsonant from '../components/Alphabet/KoreanDoubleConsonant.vue'
import KoreanVowel from '../components/Alphabet/KoreanVowel.vue'
import KoreanDoubleVowel from '../components/Alphabet/KoreanDoubleVowel.vue'
import KoreanLetter from '../components/Alphabet/KoreanLetter.vue'

// 받아쓰기
import Dictation from '../components/Dictation/Dictation.vue'
// import DictationDetail from '../components/Dictation/DictationDetail.vue'
import DictationTest from '../components/Dictation/DictationTest.vue'

import Loginout from '../components/Loginout.vue'
import Join from '../components/Join.vue'
//
import PointMaker from '../components/PointMaker.vue'
// 클래스
import ClassDetail from '../components/Class/ClassDetail.vue'
import ClassInner from '../components/Class/ClassInner.vue'
import ClassModify from '../components/Class/ClassModify.vue'
import LoadingIcon from '@/components/LoadingIcon.vue'
import VoiceRecord from '@/components/VoiceRecord.vue'

import ClassUserDetail from '../components/Class/ClassUserDetail.vue'
import ClassStudents from '../components/Class/ClassStudents.vue'
import ClassCurriculum from '../components/Class/ClassCurriculum.vue'
import ClassTimeTable from '../components/Class/ClassTimeTable.vue'
import ClassHomework from '../components/Class/ClassHomework.vue'
import ClassNotice from '../components/Class/ClassNotice.vue'
import ClassJoinRequire from '../components/Class/ClassJoinRequire.vue'
import ClassHwCreate from '../components/Class/ClassHwCreate.vue'
import CreateProblem from '../components/Class/CreateProblem.vue'

import MyChildren from '../components/MyChildren.vue'
import ChildrenDetail from '../components/ChildrenDetail.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/loading',
    component: LoadingIcon
  },
  {
    path: '/record',
    component : VoiceRecord
  },
  {
    path:'/pointmaker',
    component: PointMaker
  },
  // views
  {
    path: '/',
    name: 'main',
    component: Main
  },
  {
    path: '/contentsmain',
    name: 'contentsmain',
    component: ContentsMain
  },
  {
    path: '/classinner/:classno',
    name: 'Classinner',
    component: ClassInner
  },
  {
    path: '/classsearch',
    name: 'Classsearch',
    component: ClassSearch
  },
  {
    path: '/login',
    name: 'Loginout',
    component: Loginout
  },
  {
    path: '/join',
    name: 'join',
    component: Join
  },
  {
    path: '/draw',
    name : 'Draw',
    component: Draw
  },
  {
    path : '/alphabetwriting',
    component: AlphabetWriting
  },
  {
    path: '/studentmain',
    name: 'StudentMain',
    component: () => import('../views/StudentMain.vue')
  },
  {
    path: '/teachermain',
    name: 'TeacherMain',
    component: () => import('../views/TeacherMain.vue')
  },
  {
    path: '/class',
    name: 'class',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Class.vue'),
  },
  {
    path: '/class/modify/:classNo',
    name: 'classmodify',
    component : ClassModify
  },
  {
    path: '/wordtest/:categoryNo',
    name: 'WordTest',
    props:true,
    component: () => import('../views/WordTest.vue')
  },
  {
    path: '/drawingword',
    name: 'drawingword',
    props:true,
    component: () => import('../views/DrawingWord.vue')
  },
  {
    path: '/wordCategory',
    name: 'WordCategory',
    component: () => import('../views/WordCategory.vue')
  },
  
  //components
  {
    path: '/logo',
    name: 'logo',
    component: Logo
  },
  // 글자공부
  {
    path: '/alphabet',
    name: 'alphabet',
    component: Alphabet
  },
  {
    path: '/koreanconsonant/',
    name: 'koreanconsonant',
    component: KoreanConsonant
  },
  {
    path: '/koreandoubleconsonant/',
    name: 'koreandoubleconsonant',
    component: KoreanDoubleConsonant
  },
  {
    path: '/koreanvowel/',
    name: 'koreanvowel',
    component: KoreanVowel
  },
  {
    path: '/koreandoublevowel/',
    name: 'koreandoublevowel',
    component: KoreanDoubleVowel
  },
  {
    path: '/koreanletter/',
    name: 'koreanletter',
    component: KoreanLetter
  },
  {
    path: '/koreandetail/:koreanno',
    name: 'koreandetail',
    component: KoreanDetail
  },
  // 받아쓰기
  {
    path: '/dictation',
    name: 'dictation',
    component: Dictation
  },
  
  {
    path: '/dictationtest/:questionsno',
    name: 'dictationtest',
    props:true,
    component: DictationTest
  },
  // {
  //   path: '/dictationdetail/:questionsno',
  //   name: 'dictationdetail',
  //   component: DictationDetail
  // },

  // 클래스
  {
    path: '/classdetail/:classno',
    name: 'classdetail',
    component: ClassDetail,
    children : [
      {
        path: '/',
        name: 'ClassCurriculum',
        component: ClassCurriculum,
      },
      {
        path: '/curriculum/:classno',
        name: 'ClassCurriculum',
        component: ClassCurriculum,
      },
      {
        path: '/timetable/:classno',
        name: 'ClassTimeTable',
        component: ClassTimeTable,
      },
      {
        path: '/homework/:classno',
        name: 'ClassHomework',
        component: ClassHomework,
      },
      {
        path: '/classhwcreate/:classno',
        name: 'classhwcreate',
        component: ClassHwCreate,
      },
      {
        path: '/notice/:classno',
        name: 'ClassNotice',
        component: ClassNotice,
      },
      {
        path: '/member/:classno',
        name: 'ClassStudents',
        component: ClassStudents,
      },
      {
        path: '/joinwantlist/:classno',
        name: 'ClassJoinRequire',
        component: ClassJoinRequire,
      },
      {
        path: '/classmember/:userno',
        name: 'ClassUserDetail',
        component: ClassUserDetail
      },
      {
        path: '/createproblem/:classno',
        name: 'CreateProblem',
        component: CreateProblem
      },
    ]
  },

  {
    path: '/mychildren',
    name: 'mychildren',
    component: MyChildren
  },
  {
    path: '/childrendetail',
    name: 'childrendetail',
    component: ChildrenDetail
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
