<template>
  <div>
    학생목록
    <template>
      <v-simple-table>
        <template v-slot:default>
          <tbody>
            <tr
              v-for="member in memberlist"
              :key="member.userId"
            >
              <template>
                <v-card
                  class="mx-auto"
                  max-width="300"
                >
                  <v-img
                    :src="url + member.profileImg"
                    height="200px"
                  ></v-img>

                  <v-card-title>
                    {{member.userName}}({{ member.userId }}) 별명 : {{ member.nickName }}
                  </v-card-title>

                  <v-card-subtitle>
                    <v-btn @click="rejectjoin(member.userNo)">추방</v-btn>
                  </v-card-subtitle>

                  <v-card-actions>
                    <v-btn
                      color="orange lighten-2"
                      text
                    >
                      상세보기
                    </v-btn>

                    <v-spacer></v-spacer>

                    <v-btn
                      icon
                      @click="show = !show"
                    >
                      <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                    </v-btn>
                  </v-card-actions>

                  <v-expand-transition>
                    <div v-show="show">
                      <v-divider></v-divider>

                      <v-card-text>
                        평균 점수 : 10점
                        받아쓰기 점수 : 40점
                      </v-card-text>
                    </div>
                  </v-expand-transition>
                </v-card>
              </template>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </template>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return{
      classInfo : [],
      memberlist : [],
      show: false,
    }
  },
  created(){
    this.get_member()
  },
  methods :{
    get_member(){
      axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
            if(this.classInfo.userNo == this.userInfo.userNo){ //관리자일때
                axios.get(`class/memberlist/`+this.$route.params.classno) //해당클래스 회원들 불러오기
                .then((res)=>{
                    this.memberlist = res.data
                    console.dir(res.data)
                })
                .catch((err)=>{
                    err
                })
            }
        })
        .catch((error)=>{
            error
        })
    },
    rejectjoin(userno){
        let data = {
                classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 0,
            }
            axios.put('class/classjoin',data)
            .then((Response)=>{
                Response
                alert("추방완료.");
                this.$router.go()
            })
        }
  },
  computed : {
    userInfo(){
      return this.$store.getters.userInfo
    },
    url(){
      return this.$store.getters.BACKEND_URL
    }
  }
}
</script>

