<template>
  <div class="contentsmain">
    <v-img :src="back">
      <v-container>
        <v-row>
          <v-col class="left">
                <router-link to="/#"><logo class="logo"></logo></router-link>
          </v-col>
          <v-col class="right" v-if="userInfo">
            <ul>
              <template v-if="userInfo.userType==='teacher'">
                <v-btn rounded color="success" @click="classhometeacher">클래스홈</v-btn>
              </template>
              <template v-if="userInfo.userType==='student'">
                <v-btn rounded color="success" @click="classhomestudent">클래스홈</v-btn>
              </template>
              <v-btn rounded color="primary" @click="logout">로그아웃</v-btn><br>
              <template v-if="userInfo.userType==='teacher'">
                <v-btn rounded color="error" @click="childrendetail">학습현황</v-btn>
              </template>
              <template v-if="userInfo.userType==='student'">
                <v-btn rounded color="error" @click="mystudy">학습현황</v-btn>
              </template>
                <v-btn rounded color="warning" @click="draw">그림놀이</v-btn>
            </ul>
          </v-col>
        </v-row>
      </v-container>
      <contents/>
    </v-img>
  </div>
</template>

<script>
import Contents from '@/components/Contents.vue'
// import MakeParent from '@/components/MakeParent.vue'
import Logo from '@/components/Logo.vue'
export default {
  name: 'contentsmain',
  components: {
    Contents,
    // MakeParent,
    Logo
  },
  data() {
    return {
      back: require("../assets/images/background.jpg"),
      back1: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/38816/image-from-rawpixel-id-2210775-jpeg.jpg",
    }
  },
  methods : {
    mystudy(){
      this.$router.push('/childrendetail')
    },
    logout(){
        this.$session.destroy();
        this.$store.dispatch('LOGOUT');
        this.$router.push('/');
    },
    classhometeacher() {
      this.$router.push('/teachermain')
    },
    classhomestudent() {
      this.$router.push('/studentmain')
    },
    childrendetail() {
      this.$router.push('/mychildren')
    },
    draw() {
      this.$router.push('/draw')
    },
  },
  computed : {
    userInfo(){
      return this.$store.getters.userInfo
    }
  }
}
</script>

<style scoped>
div.container {
  padding: 0;
}
.v-img {
  width: 100%;
  height: 100%;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

ul {
  /* display: inline-block; */
  margin-left: 1%;
  padding: 1%;
}
.logo{
  height: 100% !important;
  width: 100% !important;
}

</style>