import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    BACKEND_URL : 'https://k3d206.p.ssafy.io',
    // BACKEND_URL : 'http://localhost:9999/dodam',
    // BACKEND_URL : 'http://localhost:9999',
    // BACKEND_URL : 'http://192.168.0.16:9999', // 정훈이집
    // BACKEND_URL : 'http://172.30.1.11:9999',
    // BACKEND_URL : 'http://192.168.1.2:9999',
    // BACKEND_URL : 'http://192.168.100.70:9999', // 정훈 ssafy url
    userInfo : null,
    class : null
  },
  getters: {
    BACKEND_URL(state){
      return state.BACKEND_URL
    },
    userInfo(state) {
      return state.userInfo
    },
    classInfo(state){
      return state.class
    }
  },
  mutations: {
    LOGIN(state, payload) {
      state.userInfo = payload;
    },
    LOGOUT(state) {
      state.userInfo = null;
    },
    CLASS(state, payload) {
      state.class = payload;
    },
  },
  actions: {
    LOGIN(context, data) {
      context.commit("LOGIN", data);
    },
    LOGOUT(context) {
      context.commit("LOGOUT");
    },
    CLASS(context, data) {
      context.commit("CLASS", data);
    },
  },
  modules: {
  }
})
