<template>
  <v-container class="wrapper">
    <v-row>
      <v-col cols="8">
        <div>
          <!-- <v-img :src="$store.getters.BACKEND_URL + '/letter/' + koreanlist[idx].name + '.png'" aspect-ratio="1" class="koreanimg"></v-img> -->
          <alphabet-writing ref="canvas" v-if="flag" :letter="koreanlist[koreanno].name"></alphabet-writing>
          <!-- <article><span>{{koreanlist[this.koreanno].name}}</span></article> -->
          <v-img src="../../assets/images/whale.png" aspect-ratio="1" class="img-whale">
            <div class="whale-content">
              <h2>{{koreanlist[koreanno].text}}</h2>
            </div>
          </v-img>
        </div>
      </v-col>
    </v-row>
    <div class="move-btn">
      <template v-if="koreanno == 13 || koreanno == 23 || koreanno == 28 || koreanno == 39 || koreanno == 53">
        <v-btn @click="behind">이전</v-btn>
        <v-btn @click="finish">완료</v-btn>
      </template>
      <template v-else-if="koreanno < 53">
        <v-btn @click="behind">이전</v-btn>
        <v-btn @click="next">다음</v-btn>
      </template>
    </div>
  </v-container>
</template>

<script>
import AlphabetWriting from '@/components/AlphabetWriting'

export default {
  name: 'koreandetail',
  components:{'AlphabetWriting':AlphabetWriting},
  data(){
    return {
      flag: true,
      koreanno : 0,
      koreanlist: [
        { id:"0", name: 'ㄱ', text: '기역' }, 
        { id:"1", name: 'ㄴ', text: '니은' }, 
        { id:"2", name: 'ㄷ', text: '디귿' }, 
        { id:"3", name: 'ㄹ', text: '리을' }, 
        { id:"4", name: 'ㅁ', text: '미음' }, 
        { id:"5", name: 'ㅂ', text: '비읍' }, 
        { id:"6", name: 'ㅅ', text: '시옷' }, 
        { id:"7", name: 'ㅇ', text: '이응' }, 
        { id:"8", name: 'ㅈ', text: '지읒' }, 
        { id:"9", name: 'ㅊ', text: '치옻' }, 
        { id:"10", name: 'ㅋ', text: '키읔' }, 
        { id:"11", name: 'ㅌ', text: '티읕' }, 
        { id:"12", name: 'ㅍ', text: '피읖' }, 
        { id:"13", name: 'ㅎ', text: '히읗' }, 
        { id:"14", name: 'ㅏ', text: '아' }, 
        { id:"15", name: 'ㅑ', text: '야' }, 
        { id:"16", name: 'ㅓ', text: '어' }, 
        { id:"17", name: 'ㅕ', text: '여' }, 
        { id:"18", name: 'ㅗ', text: '오' }, 
        { id:"19", name: 'ㅛ', text: '요' }, 
        { id:"20", name: 'ㅜ', text: '우' }, 
        { id:"21", name: 'ㅠ', text: '유' }, 
        { id:"22", name: 'ㅡ', text: '으' }, 
        { id:"23", name: 'ㅣ', text: '이' }, 
        { id:"24", name: 'ㄲ', text: '쌍기역' }, 
        { id:"25", name: 'ㄸ', text: '쌍디귿' }, 
        { id:"26", name: 'ㅃ', text: '쌍비읍' }, 
        { id:"27", name: 'ㅆ', text: '쌍시옷' }, 
        { id:"28", name: 'ㅉ', text: '쌍지읒' }, 
        { id:"29", name: 'ㅐ', text: '애' }, 
        { id:"30", name: 'ㅒ', text: '얘' }, 
        { id:"31", name: 'ㅔ', text: '에' }, 
        { id:"32", name: 'ㅖ', text: '예' }, 
        { id:"33", name: 'ㅘ', text: '와' }, 
        { id:"34", name: 'ㅙ', text: '왜' }, 
        { id:"35", name: 'ㅚ', text: '외' },         
        { id:"36", name: 'ㅝ', text: '워' }, 
        { id:"37", name: 'ㅞ', text: '웨' }, 
        { id:"38", name: 'ㅟ', text: '위' }, 
        { id:"39", name: 'ㅢ', text: '의' },
        { id:"40", name: '가', text: '가' }, 
        { id:"41", name: '나', text: '나' }, 
        { id:"42", name: '다', text: '다' }, 
        { id:"43", name: '라', text: '라' }, 
        { id:"44", name: '마', text: '마' }, 
        { id:"45", name: '바', text: '바' }, 
        { id:"46", name: '사', text: '사' }, 
        { id:"47", name: '아', text: '아' }, 
        { id:"48", name: '자', text: '자' }, 
        { id:"49", name: '차', text: '차' }, 
        { id:"50", name: '카', text: '카' }, 
        { id:"51", name: '타', text: '타' }, 
        { id:"52", name: '파', text: '파' }, 
        { id:"53", name: '하', text: '하' }, 
      ],
    }
  },
  created(){
    this.koreanno = this.$route.params.koreanno
    for(let i in this.koreanlist){
      if(this.koreanlist[i].id == this.koreanno){
        this.koreanno = i
        break
      }
    }
  },
  methods: {
    recreate(){
      this.flag = false;
      let timerId = setInterval(()=>{
                    if(!this.$refs.canvas){
                        clearInterval(timerId)
                        this.flag = true;
                    }
                },100)
      
      
    },
    next() {
      this.koreanno = parseInt(this.koreanno) + parseInt(1)
      this.$router.push(`/koreandetail/${this.koreanno}`)
      this.recreate()
    },
    behind() {
      if(this.koreanno == 0){
        return;
      }
      if(this.koreanno == 14){
       return;
      }
      if(this.koreanno == 24){
       return;
      }
      if(this.koreanno == 29){
        return;
      }
      if(this.koreanno == 40){
       return;
      }
      this.koreanno = this.koreanno - 1
      this.recreate()
    },
    finish() {
      if(this.koreanno == 13){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 23){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 28){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 39){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 53){
        this.$router.push('/alphabet')
      }
    }
  }
}
</script>

<style scoped>
section {
  display: table-cell;
  box-sizing: border-box;
  margin: auto;
  width: 100%;
  height: 80%;
  background-color: cadetblue;
}
article {
  font-size: 50px;
}
span {
  font-size: 250px;
}

.koreanimg {
  width: 400px;
  height: 400px;
}

.img-whale{
  position: relative;                                                            
  width: 200px;
  height: 200px;
  background-size: cover;
}

.whale-content{
  position: absolute;
  top:60%;
  left:40%;
  transform: translate(-50%, -50%);                                                                   
  font-size:5rem;
  z-index: 2;
  text-align: center;
}
.wrapper{
  padding : 1vh auto !important;
}
.row{
  padding : auto !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img-whale{
  margin: 1vh auto !important;
}
.move-btn{
  height: 8vh !important;
}

</style>