<template>
  <div>
    <v-card>수업기간</v-card>
    {{classInfo.startDate}} ~ {{classInfo.endDate}}
    <v-card>클래스 소개</v-card>
    {{classInfo.classDesc}}
    <v-card>
      <p>
      <template>
      <v-row justify="center">
        <v-dialog
          v-model="dialog"
          persistent
          max-width="600px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              v-bind="attrs"
              v-on="on"
            >
              세션추가
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">User Profile</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      label="세션이름"
                      required
                      v-model="sessionName"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      label="세션내용"
                      v-model="sessionDesc"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="dialog = false"
              >
                닫기
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="addSession"
              >
                추가
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
</template>
</p>
    </v-card>
      <template v-for="(session,index) in sessionInfo">
        <p :key="index">{{session.sessionName}} - {{session.sessionDesc}}<v-btn
      class="mx-2"
      fab
      dark
      small
      color="primary"
      @click="delSession(session.sessionNo)"
    >
      <v-icon dark>
        mdi-minus
      </v-icon>
    </v-btn></p>
      </template>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return {
      classInfo : [],
      sessionInfo : [],
      dialog: false,
    }
  },
  created() {
    this.get_classInfo();
    this.get_classsession();
  },
  methods : {
    get_classInfo(){
      axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
        })
        .catch((error)=>{
            error
        })
    },
    get_classsession(){
      axios.get(`classsession/selectclasssession/`+this.$route.params.classno)
      .then((Response)=>{
        this.sessionInfo = Response.data
      })
      .catch((error)=>{
        error
      })
    },
    addSession(){
      this.dialog = false
      let data = {
        sessionName : this.sessionName,
        sessionDesc : this.sessionDesc,
        classNo : this.$route.params.classno
      }
      axios.post('classsession/classsession',data)
      .then((Response)=>{
        Response
        alert('세션 추가 완료')
        this.$router.go()
      })
      .catch((error)=>{
        error
      })
    },
    delSession(no){
      axios.delete('classsession/oneclasssession/'+no)
      .then((Response)=>{
        Response
        alert('세션 삭제 완료')
        this.$router.go()
      })
      .catch((error)=>{
        error
      })
    }
  }
}
</script>

